import {
  Component,
  OnInit,
  ViewChild,
  ViewEncapsulation,
  Input
} from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ConstantsService } from "../common/services/constants.service";
import { IssuesService } from "../common/services/rest-api.service";

@Component({
  selector: "app-table-list",
  templateUrl: "./table-list.component.html",
  styleUrls: ["./table-list.component.scss"],
  encapsulation: ViewEncapsulation.None
})
export class TableListComponent implements OnInit {
  displayedColumns: string[] = [
    "priority",
    "ElapsedDays",
    "title",
    "status",
    "category",
    "ta",
    "studyNo",
    "vendor"
  ];
  dataSource: MatTableDataSource<any>;

  tableData: any = [];

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  @Input()
  sectionType: string;

  jiraAPI: string;

  constructor(
    private _restAPI: IssuesService,
    private _constant: ConstantsService
  ) {}

  ngOnInit() {
    if (this.sectionType === "assignedToMe") {
      this.getAllassignedData();
    } else {
      this.getAllTicketReportedByme();
    }
    console.log("const", this.jiraAPI);
    console.log("section", this.sectionType);
  }

  showTicketDetails(popover, row: any[]) {
    if (popover.isOpen()) {
      popover.close();
    } else {
      popover.open({ row });
    }
  }

  getAllassignedData() {
    this._restAPI.getAllAssignedTicket().subscribe(data => {
      this.tableData = data;
      this.dataSource = new MatTableDataSource(this.tableData.issues);
      console.log("this.dataSource: ", this.dataSource);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }
  getAllTicketReportedByme() {
    this._restAPI.getAllTicketReportedByme().subscribe(data => {
      this.tableData = data;
      this.dataSource = new MatTableDataSource(this.tableData.issues);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    });
  }

  rowLink(row){
    console.log('row link: ', row);
    return `/issue-details/${1}`;
  }
}
