import { from } from 'rxjs';
import { ConstantsService } from './common/services/constants.service';
import { DialogRaisanissueComponent } from './dialog-raisanissue/dialog-raisanissue.component';
import { DialogOverviewExampleDialog } from './shared/components/dialog/dialog.component';
import {MatDialogModule} from '@angular/material/dialog';
import { MatPaginatorModule } from '@angular/material/paginator';
import { MatIconModule } from '@angular/material/icon';
import {MatTabsModule} from '@angular/material/tabs';
import {MatTableModule} from '@angular/material/table';
import {MatSelectModule} from '@angular/material/select';
import {MatAutocompleteModule} from '@angular/material/autocomplete';
import {MatInputModule, MatProgressSpinnerModule} from '@angular/material';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { NoopAnimationsModule, BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { BreadcrumbComponent } from './shared/components/breadcrumb/breadcrumb.component';
import { IssuesComponent } from './issues/issues.component';
import { TableListComponent } from './table-list/table-list.component';
import { FooterComponent } from './shared/components/footer/footer.component';
import { MatSortModule } from '@angular/material/sort';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';
import { RaiseAnIssueComponent } from './raise-an-issue/raise-an-issue.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { IssueDetailsComponent } from './issue-details/issue-details.component';
import { PopoverComponentComponent } from './popover-component/popover-component.component';
import { SharedModule } from './shared/shared.module';
import { LoaderComponent } from './common/loader/loader.component';
import { LoaderService } from './common/services/loader.service';
import {LoaderInterceptor} from './common/interceptors/loader.interceptor';

@NgModule({
  declarations: [
    AppComponent,
    BreadcrumbComponent,
    IssuesComponent,
    TableListComponent,
    RaiseAnIssueComponent,
    IssueDetailsComponent,
    DialogOverviewExampleDialog,
    DialogRaisanissueComponent,
    PopoverComponentComponent,
    LoaderComponent
  ],
  entryComponents: [DialogOverviewExampleDialog, DialogRaisanissueComponent],
  imports: [
    BrowserModule,
    AppRoutingModule,
    NoopAnimationsModule,
    BrowserAnimationsModule,
    MatIconModule,
    MatTabsModule,
    MatTableModule,
    MatPaginatorModule,
    MatSortModule,
    MatSelectModule,
    HttpClientModule,
    NgbModule,
    FormsModule,
    ReactiveFormsModule,
    MatAutocompleteModule,
    MatInputModule,
    MatDialogModule,
    SharedModule,
    MatProgressSpinnerModule
  ],
  providers: [ConstantsService, LoaderService,
    { provide: HTTP_INTERCEPTORS, useClass: LoaderInterceptor, multi: true }],
  bootstrap: [AppComponent]
})
export class AppModule { }
