import { Component, OnInit } from "@angular/core";
import { MatDialog } from "@angular/material/dialog";

import { DialogOverviewExampleDialog } from "../shared/components/dialog/dialog.component";
import { IssuesService } from "../common/services/rest-api.service";
import { ActivatedRoute } from "@angular/router";
import * as moment from "moment";
import {
  Issue,
  IssueStatuses,
  IssueFields
} from "../shared/models/issue.model";
import { FormBuilder, FormGroup } from "@angular/forms";
import { DownstreamActivities } from "../shared/models/downstream-activities.model";
import { RootCauseCategories } from "../shared/models/root-cause-categories.model";
import { CustomFields } from "../shared/models/custom-issue-fields.enum";

@Component({
  selector: "app-issue-details",
  templateUrl: "./issue-details.component.html",
  styleUrls: ["./issue-details.component.scss"]
})
export class IssueDetailsComponent implements OnInit {
  editForm: FormGroup;

  issueComment = "";
  issue: Issue = null;
  currentLastCommentNum: number = 4;
  commentPerPage: number = 4;

  // TODO resolver class
  resolversList = null;

  issueStatusesOptions = [
    {
      label: IssueStatuses.IN_PROGRESS
    },
    {
      label: IssueStatuses.PENDING_CLOSURE
    }
  ];
  issueStatuses = IssueStatuses;

  downstreamActivities = DownstreamActivities;
  rootCauseCategory = RootCauseCategories;

  constructor(
    public fb: FormBuilder,
    public dialog: MatDialog,
    public _restApi: IssuesService,
    public activatedRoute: ActivatedRoute
  ) {}

  ngOnInit() {
    this.getDetails();
    this.getResolversList();
    this.createForm();
  }

  getDetails() {
    let fieldsString = `?fields=${Object.keys(new IssueFields()).join(
      ","
    )},${Object.keys(CustomFields)
      .map(k => CustomFields[k])
      .join(",")}`;

      Object.keys(new IssueFields()).forEach(r => console.log(r));
console.log(fieldsString);
    this._restApi
      .getOneIssue(this.activatedRoute.snapshot.params.id, fieldsString)
      .subscribe(
        issueData => {
          console.log("issue: ", issueData);
          this.issue = issueData;
          console.log(this.editForm.get("status"));
          this.StatusCtrl.setValue(this.issue.Status);
        },
        err => this._restApi.errorHandler(err)
      );
  }

  getResolversList() {
    this._restApi.getResolversList().subscribe(
      (r: any) => {
        // TODO response interface
        console.log("resoveler list: ", r);
        this.resolversList = r.actors;
      },
      err => this._restApi.errorHandler(err)
    );
  }

  // #region ACTION HANDLER
  getMoreComments() {
    console.log("getMoreComments! last num ", this.currentLastCommentNum);
    this.currentLastCommentNum += this.commentPerPage;
  }

  changeStatusHandler() {
    console.log("change status: ", this.StatusCtrl.value);
  }
  // #endregion ACTION HANDLERs

  // submit form
  submitForm(): void {
    console.log("SubmitForm");
    let downstreamActs = this.editForm.get("downStreamActs").value;
    console.log("values d.acts: ", downstreamActs);

    const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
      width: "870px",
      panelClass: "custom-modalbox"
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log("The dialog was closed");
    });
  }

  createForm() {
    this.editForm = this.fb.group({
      status: [],
      assignee: [],
      downStreamActs: this.createDownStreamActivities(),
      rootCauseCats: this.createRootCauseCategories()
    });
    console.log(this.DownstreamActs);
  }

  createDownStreamActivities() {
    let arr = [];
    console.log(this.downstreamActivities);
    Object.keys(this.downstreamActivities).forEach(act => {
      arr.push(this.fb.control(false));
    });
    console.log(arr);
    return this.fb.array(arr);
  }

  createRootCauseCategories() {
    let arr = [];

    Object.keys(this.rootCauseCategory).forEach(act => {
      arr.push(this.fb.control(false));
    });
    console.log(arr);
    return this.fb.array(arr);
  }

  // getters
  get StatusCtrl() {
    return this.editForm.get("status");
  }

  get Assignee() {
    return this.editForm.get("assignee");
  }

  get DownstreamActs() {
    return this.editForm.get("downStreamActs");
  }

  get RootCauseCats() {
    return this.editForm.get("rootCauseCats");
  }

  getActivityTitle(index) {
    return this.downstreamActivities[
      Object.keys(this.downstreamActivities).filter((v, i) => {
        if (i === index) return v;
      })[0]
    ];
  }

  getCategoryTitle(index) {
    return this.rootCauseCategory[
      Object.keys(this.rootCauseCategory).filter((v, i) => {
        if (i === index) return v;
      })[0]
    ];
  }
}
