import { Injectable } from "@angular/core";

@Injectable({
  providedIn: "root"
})
export class ConstantsService {
  private readonly jiraAPI: string =
    "https://jira.intranet.roche.com/jira/rest/api/2/";
  private readonly nodeServerApi: string = "http://10.95.53.182:3000/";

  readonly nodeGetIssuesApi: string = this.nodeServerApi + "getIssues";
  readonly nodeGetOneIssue: string = this.nodeServerApi + "getIssue";
  readonly nodeRaiseAnIssue: string = this.nodeServerApi + "raiseAnIssue";
  readonly getResolverList: string = this.nodeServerApi + "getResolvers";

  readonly jiraProjectKey: string = "IMSS";

  // to move on serve side
  readonly jiraGetAllAssignedTicket: string =
    this.jiraAPI +
    "search/?jql=project = IMSS AND resolution = Unresolved AND assignee in (currentUser()) order by updated DESC";
  readonly jiraGetAllReportedByMeTicket: string =
    this.jiraAPI +
    "search/?jql=project = IMSS AND reporter in (currentUser()) order by updated DESC";
  readonly jiraCreateIssue: string = this.jiraAPI + "issue/";
  readonly jiraGetOneIssue: string = this.jiraAPI + "issue/";

  constructor() {}
}
