import { CustomFields } from "./custom-issue-fields.enum";

export class Issue {
  expand: string;
  fields: IssueFields;
  id: string;
  key: string;
  self: string;

  constructor(issue?: Issue) {
    if (issue) this.deserialize(issue);
  }

  deserialize?(issue: Issue) {
    Object.assign(this, issue);
    if (
      issue.fields.comment &&
      issue.fields.comment.comments &&
      issue.fields.comment.comments.length
    )
      this.fields.comment.comments = issue.fields.comment.comments.map(
        (comment: Comment) => new Comment(comment)
      );
    // console.log("deserialized: ", this.fields, this, this.fields.status);
  }

  get CreatedDate() {
    if (this.fields && this.fields.created) return this.fields.created;
    return "";
  }

  get Creator() {
    if (this.fields && this.fields.creator)
      return this.fields.creator.displayName;
    return "";
  }

  get Priority(): IssuePriority | string {
    if (this.fields && this.fields.priority) return this.fields.priority.name;
    return "";
  }

  get Status() {
    if (this.fields && this.fields.status && this.fields.status.name)
      return this.fields.status.name;
    return "";
  }

  get PriorityIcoPath() {
    switch (this.Priority) {
      case IssuePriority.HIGH:
        return "./assets/images/ico_high_prior.svg";
      case IssuePriority.LOW:
        return "./assets/images/ico_low_prior.svg";
      case IssuePriority.MEDIUM:
        return "./assets/images/ico_medium_prior.svg";
      default:
        return "";
    }
  }

  get CommentsList(): Comment[] {
    if (this.fields && this.fields.comment && this.fields.comment.comments)
      return this.fields.comment.comments;
    return null;
  }

  // custom fields getters
  get VendorName() {
    if (this.fields && this.fields[CustomFields.vendor])
      return this.fields[CustomFields.vendor];
    return "";
  }

  get Category() {
    if (this.fields && this.fields[CustomFields.category])
      return this.fields[CustomFields.category];
    return "";
  }

  get BusinessImpact() {
    if (this.fields && this.fields[CustomFields.business_impact])
      return this.fields[CustomFields.business_impact];
    return "";
  }

  get StudyNumber() {
    if (this.fields && this.fields[CustomFields.study_number])
      return this.fields[CustomFields.study_number];
    return "";
  }
}

export class IssueFields {
  // and defined custom fields
  resolution?: any = null;
  lastViewed?: any = null;
  aggregatetimeoriginalestimate?: any = null;
  issuelinks: any[] = null;
  subtasks: any[] = null;
  issuetype: Issuetype = null;
  timetracking: Timetracking = null;
  environment?: any = null;
  timeestimate?: any = null;
  aggregatetimespent?: any = null;
  workratio: number = null;
  labels: any[] = null;
  reporter: Reporter = null;
  watches: Watches = null;
  updated: Date = null;
  timeoriginalestimate?: any = null;
  description: string = null;
  fixVersions: any[] = null;
  priority: Priority = null;
  created: Date = null;
  attachment: any[] = null;
  assignee?: any = null;
  votes: Votes = null;
  worklog: Worklog = null;
  duedate?: any = null;
  status: Status = null;
  aggregatetimeestimate?: any = null;
  creator: Creator = null;
  timespent?: any = null;
  components: any[] = null;
  progress: Progress = null;
  project: Project = null;
  resolutiondate?: any = null;
  summary: string = null;
  comment: Comments = null;
  versions: any[] = null;
  aggregateprogress: Aggregateprogress = null;

  constructor(){}
}

export class Comment {
  self: string;
  id: string;
  author: Author;
  body: string;
  updateAuthor: UpdateAuthor;
  created: Date;
  updated: Date;

  constructor(fields?: Comment) {
    if (fields) this.deserialize(fields);
  }

  deserialize(fields?: Comment) {
    Object.assign(this, fields);
  }

  get AuthName() {
    if (this.author && this.author.name) return this.author.name;
    return "";
  }

  get AuthFullName() {
    if (this.author && this.author.displayName) return this.author.displayName;
    return "";
  }

  get AuthAvatarIco() {
    if (
      this.author &&
      this.author.avatarUrls &&
      this.author.avatarUrls["48x48"]
    )
      return this.author.avatarUrls["48x48"];
    return "";
  }

  get Content() {
    if (this.body) return this.body;
    return "";
  }
}

