// enum of custom fields
export enum CustomFields {
  category = "customfield_30781",
  root_cause = "customfield_13794",
  business_impact = "customfield_13835",
  vendor = "customfield_19566",
  study_number = "customfield_22072",
  submitter = "customfield_13999",
  submitter_role = "customfield_30861",
  vendor_instrument = "customfield_30862",
  ecoa_endpoiont = "customfield_30863",
  ecoa_modality = "customfield_30864",
  downstream_impact = "customfield_30871",
}

export const ifDefinedCustomField = (fieldName: string) => {
  return Object.keys(CustomFields).some((k) => CustomFields[k] === fieldName );
};
