import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { ConstantsService } from "./constants.service";
import { map } from "rxjs/operators";
import { Issue } from "src/app/shared/models/issue.model";
import {
  CustomFields,
  ifDefinedCustomField
} from "src/app/shared/models/custom-issue-fields.enum";

@Injectable({
  providedIn: "root"
})
export class IssuesService {
  constructor(private http: HttpClient, private _constant: ConstantsService) {}

  getAllAssignedTicket() {
    return this.http.get(this._constant.nodeGetIssuesApi, {
      params: {
        url: this._constant.jiraGetAllAssignedTicket
      }
    });
  }

  getAllTicketReportedByme() {
    return this.http.get(this._constant.nodeGetIssuesApi, {
      params: {
        url: this._constant.jiraGetAllReportedByMeTicket
      }
    });
  }

  getOneIssue(id: string, fieldsString?: string) {
    return this.http
      .get(this._constant.nodeGetOneIssue, {
        params: {
          url: `${this._constant.jiraGetOneIssue}${id}${
            fieldsString ? fieldsString : ""
          }`
        }
      })
      .pipe(
        map((r: Issue) => {
          return new Issue(this.clearCustomFields(r)) as Issue;
        })
      );
  }

  getResolversList() {
    return this.http.get(this._constant.getResolverList).pipe(
      map(r => {
        return r;
      })
    );
  }

  raiseAnIssue(data: any) {
    console.log("from service", data);
    return this.http.post(this._constant.nodeRaiseAnIssue, data);
  }

  errorHandler(err) {
    console.error(err);
  }

  clearCustomFields(resp: Issue) {
    let customFieldName = "customfield";
    let nObject: any = {};

    // not necessary var
    let numberOfCustomFields: number = 0;

    Object.keys(resp.fields).forEach((k: string) => {
      if (k.indexOf(customFieldName) === -1 || ifDefinedCustomField(k)) {
        nObject[k] = resp.fields[k];
      } else {
        numberOfCustomFields++;
      }
    });
    resp.fields = nObject;
    // not necessary log
    console.log("numberOfCustomFields: ", numberOfCustomFields);
    return resp;
  }
}
