import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";

import { IssuesComponent } from "./issues/issues.component";
import { RaiseAnIssueComponent } from "./raise-an-issue/raise-an-issue.component";
import { IssueDetailsComponent } from "./issue-details/issue-details.component";
import { NotFoundComponent } from "./shared/components/not-found-route.component";

const routes: Routes = [
  { path: "", component: IssuesComponent },
  { path: "rais-an-issue", component: RaiseAnIssueComponent },
  { path: "issue-details/:id", component: IssueDetailsComponent },
  { path: "**", component: NotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
