import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import { FormBuilder, FormGroup, Validators, FormControl, FormArray } from '@angular/forms';
import {MatDialog} from '@angular/material/dialog';
import { ConstantsService } from '../common/services/constants.service';
import {IssuesService} from '../common/services/rest-api.service';
import {Observable, from} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {DialogRaisanissueComponent} from '../dialog-raisanissue/dialog-raisanissue.component';

@Component({
  selector: 'app-raise-an-issue',
  templateUrl: './raise-an-issue.component.html',
  styleUrls: ['./raise-an-issue.component.scss']
})

export class RaiseAnIssueComponent implements OnInit {
  raiseAnIssueForm: FormGroup;
  submitted = false;

  studyNumberVal = ''
  studyNumberoptions: string[] = ["BA12345", "BA12346", "BA12347", "BA12348", "BA12349", "BA12350", "DF12351", "DF12352", "DF 12353 - Example", "DF 12354 - Acronym", "DF 12355 - Word", "DF 12356 - Example", "DF 12353 - Example", "DF 12354 - Acronym", "DF 12355 - Word", "DF 12356 - Example", "DF 12353 - Example", "DF 12354 - Acronym", "DF 12355 - Word", "DF 12356 - Example"];
  filteredStudyNumberOptions: Observable<string[]>;

  vendorVal= '';
  vendorOptions: string[] = ["Signant Health", "ER", "Kayentis", "Madidata", "YPrime"];
  filteredVendorOptions: Observable<string[]>;

  IssueImpacts: any = [
    {
      name: "Study timeline delay",
      value: "Study timeline delay"
    },
    {
      name: "Study cost increase",
      value: "Study cost increase",
    },
    {
      name: "Risk of not meeting primary or secondary endpoint",
      value: "Risk of not meeting primary or secondary endpoint",
    },
    {
      name: "Risk of not meeting other endpoints",
      value: "Risk of not meeting other endpoints"
    },
    {
      name: "Risk of not meeting regulatory data standards/submission requirements",
      value: "Risk of not meeting regulatory data standards/submission requirements"
    },
    {
      name: "Risk of compromising access and payer strategy",
      value: "Risk of compromising access and payer strategy"
    },
    {
      name: "None",
      value: "None"
    }
  ];

  constructor(
    private formBuilder: FormBuilder,
    public dialog: MatDialog,
    private _constant: ConstantsService,
    private _restAPI: IssuesService,
    private router: Router) {  }

  ngOnInit() {
    const descriptioanPlaceHolder = `1. Instrument (COA) name:
2. Instrument (COA) endpoint: primary / secondary / other
3. eCOA modality: digital pen / handheld / tablet / web-based
4. eCOA Administration: site/ home/ site and home`

    this.raiseAnIssueForm = this.formBuilder.group({
      studyNumber: ['', Validators.required],
      vendor: ['', Validators.required],
      issueCategory: [''],
      issueTitle: ['', [Validators.required]],
      description: [descriptioanPlaceHolder, [Validators.required]],
      priority: ['10101', Validators.required],
      impacts:new FormArray([])
    });

    this.filteredStudyNumberOptions = this.raiseAnIssueForm.get("studyNumber").valueChanges.pipe(
      startWith(''),
      map(value => this._filterOptions(value, this.studyNumberoptions))
    );
    this.filteredVendorOptions = this.raiseAnIssueForm.get("vendor").valueChanges.pipe(
      startWith(''),
      map(value => this._filterOptions(value, this.vendorOptions))
    );

    this.addImpactsCheckboxes();
  }
   // convenience getter for easy access to form fields
   get f() { return this.raiseAnIssueForm.controls; }

   onSubmit() {
       this.submitted = true;
        // stop here if form is invalid
       if (this.raiseAnIssueForm.invalid) {
         return;
        }

      const selectedIssueImpact = this._getSelectedIssueImpact();

       let data = {
        "url":this._constant.jiraCreateIssue,
        "postData":{"fields": {
           "project":
           {
              "key": this._constant.jiraProjectKey
           },
           "summary": this.raiseAnIssueForm.value.issueTitle,
           "description": this.raiseAnIssueForm.value.description,
           "issuetype": {
              "name": "Task"
           },
           "priority": {
            "id": this.raiseAnIssueForm.value.priority
            }
          }
        }
        }
       this._restAPI.raiseAnIssue( data )
       .subscribe(response => {
         this.raiseAnIssueForm.reset();
         this.openDialog();
       })
   }

   onReset() {
       this.submitted = false;
       this.raiseAnIssueForm.reset();
   }

   openDialog(): void {
    const dialogRef = this.dialog.open(DialogRaisanissueComponent, {
      width: '870px',
      panelClass: 'success-modalbox',
      data: this.raiseAnIssueForm.value
    });

    dialogRef.afterClosed().subscribe(result => {
      this.router.navigateByUrl('/');
    });
  }

   private _filterOptions(value: string, Listoptions: any[]): string[] {
    const filterValue = value.toLowerCase();
    return Listoptions.filter(option => option.toLowerCase().includes(filterValue));
  }
   private _getSelectedIssueImpact(): string[] {
    return this.raiseAnIssueForm.value.impacts
    .map((v: any, i: string | number) => v ? this.IssueImpacts[i].value : null)
    .filter((v: any) => v !== null);
  }

  private addImpactsCheckboxes() {
    this.IssueImpacts.forEach((_o: any, i: number) => {
      const control = new FormControl();
      (this.raiseAnIssueForm.controls.impacts as FormArray).push(control);
    });
  }


}
